<template>
  <div class="r-col" :class="{
    [`r-col-${span}`]: true,
    [`r-col-offset-${offset}`]: offset,
    [`r-col-pull-${pull}`]: pull,
    [`r-col-push-${push}`]: push,
    [`r-col-order-${order}`]: order,
  }" :style="styles">
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: 'rCol',
  props: {
    /**
     * 0~24 0 display:none
     */
    span: {
      type: Number | String,
      default: 0,
    },
    /**
     * 
     */
    order: {
      type: String,
      default: '',
    },
    /**
     * 
     */
    offset: {
      type: Number | String,
      default: '',
    },
    /**
     * 
     */
    push: {
      type: Number | String,
      default: '',
    },
    /**
     * 
     */
    pull: {
      type: Number | String,
      default: '',
    }
  },
  computed: {
    gutter() {
      let parent = this.$parent;
      
      while (parent && parent.$vnode.componentOptions.Ctor.options.name !== 'rRow') {
        parent = parent.$parent;
      }
      
      return parent ? parent.gutter : 0;
    },
    styles(){
      const styles = {}
      console.log(this.gutter)
      if(this.gutter>0){
        styles.paddingLeft = `${this.gutter/2}px`
        styles.paddingRight = styles.paddingLeft
      }
      return styles
    }
  },
}
</script>
